<template>
    <div>
        <!-- 顶部的筛选 -->
        <classify :iden="3"></classify>

        <!-- 主题内容 -->
        <common :loading="loading" :error="error" :datas="audioData"></common>
    </div>
    
</template>
<script>
    
    import Classify from '../component/classify.vue';
    import Common from '../component/common.vue';
    import OPLACE from '../../../oplace.js';
    export default {
        data() {
            return {
                audioData: [], // 音频数据
                loading: false, // 加载中
                error: null, // 加载错误
            }
        },
        created() {
            this.getAudioOplace();
        },
        methods: {

            // 获取视频作品信息
            getAudioOplace() {
                this.audioData = OPLACE.audio;
            }
        },
        components: {
            Classify,
            Common
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    .column {
        -webkit-column-count: 3;
        column-count: 3;
        -webkit-column-gap: 15px;
        column-gap: 15px;
    }
</style>



